import React from 'react';
import { Link, NavLink} from 'react-router-dom';

function Navbar() {


    return (
     
        <div className="container">
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
  <div className="container-fluid">
    <NavLink className="navbar-brand" to="/">FOOD ORDERING</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink className="nav-link active" aria-current="page" to="/">MENU</NavLink>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="#">PIZZAS</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="#">BURGERS</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="#">STARTERS</Link>
        </li>
      </ul>
       <Link to={`/CartSCreen/`}> <button className="btn btn-outline-success" type="submit">Cart</button></Link>
    </div>
  </div>
</nav>
</div>
    );
}

export default Navbar;
